









import { Component, Vue } from 'vue-property-decorator'
import * as _ from 'lodash'
import { Bird } from '@/interfaces/bird'

export const quizList: string[] = []

@Component
export default class App extends Vue {
  private quizList: string[] = quizList;
  onAddToQuiz (bird: Bird): void {
    if (!_.includes(quizList, bird.speciesCode)) {
      quizList.push(bird.speciesCode)
    }
  }
  removeFromQuiz (bird: Bird): void {
    var index = quizList.indexOf(bird.speciesCode)
    quizList.splice(index, 1)
  }
}
