































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import * as _ from 'lodash'
import { birdsJson } from '@/views/Practice.vue'
import { Bird } from '@/interfaces/bird'
import { quizList } from '@/App.vue'

@Component
export default class Quiz extends Vue {
  private bird?: Bird
  private quizList: string[] = quizList
  private sound?: any = {}
  private photo?: any = {}
  private answers?: Bird[] = []
  private pickedAnswer?: Bird

  created () {
    this.pickBird()
  }

  pickBird () {
    if (quizList.length < 3) {
      this.quizList = _.map(birdsJson, (b: Bird) => b.speciesCode)
    }
    this.pickedAnswer = undefined
    const birdsList = _.filter(birdsJson, (b) => _.includes(this.quizList, b.speciesCode))

    // chose a new bird in the list (we don't want to reuse previously chosen one)
    const otherBirds = birdsList.slice()
    _.remove(otherBirds, (n) => n === this.bird)
    this.bird = _.sample(otherBirds)
    if (!this.bird) {
      return
    }

    const sounds = _.filter(this.bird.sounds, (sound) => sound.title.toLowerCase().indexOf('song') !== -1)
    this.sound = _.sample(sounds)
    this.photo = this.bird.photos[0]

    // exclude current bird to prevent doublons
    _.remove(birdsList, (n) => n === this.bird)
    this.answers = _.shuffle([this.bird].concat(_.slice(_.shuffle(birdsList), 0, 2)))
  }

  chooseBird (chosen: Bird) {
    this.pickedAnswer = chosen
    this.$forceUpdate()
  }
}
