



























import { Component, Prop, Vue } from 'vue-property-decorator'
import * as _ from 'lodash'
import { Bird } from '@/interfaces/bird'
import { quizList } from '@/App.vue'

export const birdsJson: Bird[] = require('@/birds_list.json')

@Component
export default class List extends Vue {
  private birds: Bird[] = birdsJson
  private orders: string[] = []
  private quizList: string[] = quizList
  private lodash = _

  created () {
    this.birds = _.sortBy(this.birds, (b) => b.familyComName)
    this.orders = _.uniq(_.map(this.birds, (b) => b.order))
  }

  getFamiliesForOrder (order: string): string[] {
    const birdsForOrder = _.filter(this.birds, (b) => b.order === order)
    return _.uniq(_.map(birdsForOrder, (b) => b.familyComName))
  }

  getBirdsForFamily (family: string) {
    return _.filter(this.birds, (b) => b.familyComName === family)
  }
}
