import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Birds from './views/Birds.vue'
import Bird from './views/Bird.vue'
import Practice from './views/Practice.vue'
import PracticeBird from './views/PracticeBird.vue'
import Quiz from './views/Quiz.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/birds',
      name: 'birds',
      component: Birds
    },
    {
      path: '/birds/:code',
      name: 'bird',
      component: Bird
    },
    {
      path: '/practice',
      name: 'practice',
      component: Practice
    },
    {
      path: '/quiz',
      name: 'quiz',
      component: Quiz
    }

  ]
})
