


















import { Component, Prop, Vue } from 'vue-property-decorator'
import * as _ from 'lodash'
import { Bird } from '@/interfaces/bird'
import { quizList } from '@/App.vue'

export const birdsJson: Bird[] = require('@/birds_list.json')

@Component
export default class Practice extends Vue {
  private bird?: Bird
  private sound?: any = {}
  private photo?: any = {}
  private list: string[] = []
  private lodash = _

  created () {
    this.randomize()
    this.pickBird()
  }

  randomize () {
    let birds = quizList
    if (!birds.length) {
      birds = _.map(birdsJson, (b: Bird) => b.speciesCode)
    }
    this.list = _.shuffle(birds)
  }

  pickBird () {
    const code = this.list.shift()
    this.bird = _.find(birdsJson, (o) => o.speciesCode === code)
    if (!this.bird) {
      return
    }
    const sounds = _.filter(this.bird.sounds, (sound) => sound.title.toLowerCase().indexOf('song') !== -1 || sound.title.toLowerCase().indexOf('duet') !== -1)
    this.sound = _.sample(sounds)
    this.photo = this.bird.photos[0]
    if (this.list.length === 0) {
      this.randomize()
    }
  }
}
