








































import { Component, Prop, Vue } from 'vue-property-decorator'
import * as _ from 'lodash'
import { Bird } from '@/interfaces/bird'
import { birdsJson } from '@/views/Practice.vue'
import { quizList } from '@/App.vue'

@Component
export default class PracticeBird extends Vue {
  private sound?: any = {}
  private photo?: any = {}
  private birds: Bird[] = birdsJson
  private bird: any = {}
  private quizList: string[] = quizList
  private lodash = _

  created () {
    const code = this.$route.params.code
    this.bird = _.find(this.birds, (o) => o.speciesCode === code)
    this.sound = this.bird.sounds[0]
    this.photo = this.bird.photos[0]
  }
}
